.navbar {
    position: sticky;
    top: 0px;
    height: 70px;
    background-color: #08090E;
    opacity: 0.75;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 50px 0px 50px;
    margin-bottom: -70px;
    z-index: 5;
}
  
.navbar_logo {
    color: white;
    font-family: "MonteCarlo", cursive;
    font-size: 40px;
}
  
.navbar_list {
    display: flex; 
    background-color: #08090E;
    list-style: none;
}
  
.navbar_item {
    white-space: nowrap;
}
  
.navbar_link {
    font-size: 0.85rem;
    text-decoration: none;
    color: rgb(255, 255, 255);
    text-align: center;
    padding: 10px;
    margin: 0px 5px;
    border-style: solid;
    border-width: 0px 0px 1px 0px;
    border-color: transparent;
    transition: 250ms;
    font-size: 18px;
    -webkit-transition: 250ms;
    -moz-transition: 250ms;
    -ms-transition: 250ms;
    -o-transition: 250ms;
}
  
.navbar_link:hover {
    border-color: white;
    transition: 250ms;
    -webkit-transition: 250ms;
    -moz-transition: 250ms;
    -ms-transition: 250ms;
    -o-transition: 250ms;
}
  
.navbar_menu {
    color: white;
    display: none;
    cursor: pointer;
}


@media screen and (max-width: 800px) {
   
    .navbar_list {
        padding: 0px;
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        top: 70px;
        left: -100%;
        height: 300px;
        width: 100%;
        transition: all 0.5s ease;
        -webkit-transition: all 0.5s ease;
        -moz-transition: all 0.5s ease;
        -ms-transition: all 0.5s ease;
        -o-transition: all 0.5s ease;
}
  
    .navbar_list--active {
        left: 0%;
    }
  
    .navbar_menu {
        display: block;
    }
  
    .navbar_item {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 0;
        flex: 1;
        -webkit-border-radius: 0;
        -moz-border-radius: 0;
        -ms-border-radius: 0;
        -o-border-radius: 0;
    }
  
    .navbar_link {
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin: 0px;
        padding: 5px;
        border-width: 0px;
        background-color: black;
        opacity: 0.75;
    }
  
    .navbar_link:hover {
        border-width: 0px;
        opacity: 0.9;
    } 
}









