.certificates-container {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.certificates-title {
    font-family: "MonteCarlo", cursive;
    font-size: 30px;
    display: flex;
    justify-content: center;
    margin: 30px 0px 50px 0px;
}

@keyframes certificate {
    0% {width: 0%; height: 50%;}
    100% {width: 55%; height: 55%;}
}

.showCertificate {
    width: 55%;
    height: 400px;
    display: flex;
    justify-content: center;
    animation-name: certificate;
    animation-duration: 12s;
}


.certificates-list {
    width: 45%;
    list-style-type: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center; 
    padding: 0px;
    margin-top: 30px;
}

.certificates-item img {
    height: 100px;
    width: auto;
    cursor: pointer;
    position: cover;
    margin: 0px;
}

